.timeline {
  max-width: 1000px;
  position: relative;
}

.timeline:after {
  content: "";
  width: 6px;
  background-color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
}

.container {
  background-color: inherit;
  width: 50%;
  padding: 10px 40px;
  position: relative;
}

.container:after {
  content: "";
  width: 25px;
  height: 25px;
  z-index: 1;
  background-color: #fff;
  border: 4px solid #ff9f55;
  border-radius: 50%;
  position: absolute;
  top: 15px;
  right: -17px;
}

.left {
  width: 42%;
  left: 0;
}

.right {
  left: 50%;
}

.left:before {
  content: " ";
  height: 0;
  width: 0;
  z-index: 1;
  border: 10px solid #0000;
  border-left-color: #fff;
  border-right-width: 0;
  position: absolute;
  top: 22px;
  right: 30px;
}

.right:before {
  content: " ";
  height: 0;
  width: 0;
  z-index: 1;
  border: 10px solid #0000;
  border-left-width: 0;
  border-right-color: #fff;
  position: absolute;
  top: 22px;
  left: 30px;
}

.right:after {
  left: -16px;
}

.content {
  background-color: #fff;
  border-radius: 6px;
  padding: 20px 30px;
  position: relative;
}

.content p {
  font-size: 1.5rem;
}

@media screen and (max-width: 600px) {
  .timeline:after {
    left: 31px;
  }

  .container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  .container:before {
    border: 10px solid #0000;
    border-left-width: 0;
    border-right-color: #fff;
    left: 60px;
  }

  .left:after, .right:after {
    left: 15px;
  }

  .right {
    left: 0%;
  }

  .content {
    max-width: 60%;
  }
}

